import { v4 } from "uuid";
import { fetchConToken } from "../helpers/fetch"
import { types } from "../types/types"
import { addNotification } from "./notification";

export const eventStartAddNew = (event) => {
    return async (dispatch, getState) => {

        const { uid, name } = getState().auth

        try {

            const response = await fetchConToken('events', event, 'POST')
            const body = await response.json()

            if (!body.ok) {
                return dispatch(addNotification(v4(), 'Error al crear el evento', types.error))
            }

            event.id = body.evento.id
            event.user = {
                _id: uid,
                name: name
            }
            dispatch(eventAddNew(event))
            return dispatch(addNotification(v4(), 'Evento agregado correctamente', types.success))

        } catch (error) {
            dispatch(addNotification(v4(), 'Error al crear el evento', types.error))
        }
    }
}

const eventAddNew = (event) => {
    return {
        type: types.eventAddNew,
        payload: event
    }
}

export const setActive = (event) => {
    return {
        type: types.eventSetActive,
        payload: event
    }
}

export const unsetActive = () => {
    return {
        type: types.eventUnsetActive
    }
}

export const startEventUpdated = (event) => {
    return async (dispatch) => {

        try {
            const response = await fetchConToken(`events/${event.id}`, event, 'PUT')
            const body = await response.json()

            if (!body.ok) {
                return dispatch(addNotification(v4(), body.msg, types.error))
            }

            dispatch(eventUpdated(event))
            dispatch(addNotification(v4(), 'Evento actualizado', types.success))

        } catch (error) {
            console.log(error);
            return dispatch(addNotification(v4(), 'Error al actualizar el evento', types.error))
        }

    }
}

const eventUpdated = (event) => {
    return {
        type: types.eventUpdate,
        payload: event
    }
}

export const eventDeleted = () => {
    return {
        type: types.eventDelete
    }
}

export const eventStartLoading = () => {
    return async (dispatch) => {
        try {
            const response = await fetchConToken('events')
            const body = await response.json()
            if (!body.ok) {
                return dispatch(addNotification(v4(), 'Error al cargar los eventos', types.error))
            }
            const eventos = body.eventos
            dispatch(loadingEvents(eventos))
            dispatch(addNotification(v4(), 'Eventos cargados correctamente', types.success))
        } catch (error) {
            console.log(error);
            return dispatch(addNotification(v4(), 'Error al cargar los eventos', types.error))
        }

    }
}

const loadingEvents = (events) => {
    return {
        type: types.eventsLoaded,
        payload: events
    }
}

