import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { v4 } from 'uuid';
import validator from 'validator';
import { useForm } from '../../hooks/useForm';
import { addNotification } from '../../actions/notification';
import { types } from '../../types/types';
import { startRegister } from '../../actions/auth';


export const Register = () => {

    const dispatch = useDispatch()

    const initialState = {
        name: '',
        email: '',
        password: '',
        password2: ''
    }

    const [ formValues, handleInputChange ] = useForm(initialState)
    const [isNameValid, setIsNameValid] = useState(true);
    const [isEmailValid, setIsEmailValid] = useState(true);
    const [isPasswordValid, setIsPasswordValid] = useState(true);
    const [isPassword2Valid, setIsPassword2Valid] = useState(true);

    const { name, email, password, password2 } = formValues

    const handleSubmit = (e) => {
        e.preventDefault()
        if(validarCampos()) {
            dispatch(startRegister(name, email, password))
        }

    }

    const validarCampos = () => {

        if( name.trim().length <= 2 ) {
            setIsNameValid(false)
            dispatch(addNotification(v4(), 'El nombre de ha tener al menos 3 carácteres', types.error))
            return false
        }
        setIsNameValid(true)

        if( !validator.isEmail(email) ) {
            setIsEmailValid(false)
            dispatch(addNotification(v4(), 'Correo no válido', types.error))
            return false
        }
        setIsEmailValid(true)

        if( password.trim().length <= 5 ) {
            setIsPasswordValid(false)
            dispatch(addNotification(v4(), 'La contraseña ha de ser de al menos 6 carácteres', types.error))
            return false
        }
        setIsPasswordValid(true)

        if( password2 !== password ) {
            setIsPassword2Valid(false)
            dispatch(addNotification(v4(), 'Las contraseñas no coinciden', types.error))
            return false
        }
        setIsPassword2Valid(true)

        return true

    }

    return (
        <div className="col-md-6 login-form-2">
            <h3>Registro</h3>
            <form
                onSubmit={handleSubmit}
                noValidate
            >
                <div className="form-group">
                    <input
                        type="text"
                        className={`form-control ${!isNameValid ? 'formBorderValidationError' : '' } `}
                        placeholder="Nombre"
                        name='name'
                        onChange={handleInputChange}
                        
                    />
                </div>
                <div className="form-group">
                    <input
                        type="email"
                        className={`form-control ${!isEmailValid ? 'formBorderValidationError' : '' } `}
                        placeholder="Correo"
                        name='email'
                        onChange={handleInputChange}
                    />
                </div>
                <div className="form-group">
                    <input
                        type="password"
                        className={`form-control ${!isPasswordValid ? 'formBorderValidationError' : '' } `}
                        placeholder="Contraseña"
                        name='password'
                        onChange={handleInputChange}
                    />
                </div>

                <div className="form-group">
                    <input
                        type="password"
                        className={`form-control ${!isPassword2Valid ? 'formBorderValidationError' : '' } `}
                        placeholder="Repita la contraseña"
                        name='password2'
                        onChange={handleInputChange}
                    />
                </div>

                <div className="form-group">
                    <input
                        type="submit"
                        className="btnSubmit"
                        value="Crear cuenta" />
                </div>
            </form>
        </div>
    )
};
