import moment from 'moment';
import { useEffect, useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { eventStartAddNew, startEventUpdated, unsetActive } from '../../actions/events';
import { uiCloseModal } from '../../actions/ui';

const now = moment().minutes(0).seconds(0).add(1, 'hours') //03:00:00
const nowPlusOne = moment().minutes(0).seconds(0).add(2, 'hours') //04:00:00

const initModalContent = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: nowPlusOne.toDate()
}


export const CalendarModalContent = () => {

    const dispatch = useDispatch()
    const { activeEvent } = useSelector( state => state.calendar )



    const [ startDate, setStartDate ] = useState( now.toDate() );
    const [ endDate, setEndDate ] = useState( nowPlusOne.toDate() );

    //States para la validación
    const [endDateIsValid, setEndDateIsValid] = useState(true);
    const [titleIsValid, setTitleIsValid] = useState(true);


    const [ formValues, setFormValues ] = useState(initModalContent);

    const { title, notes } = formValues

    const handleStartDate = (e) => {
        setStartDate(e)
        setFormValues({
            ...formValues,
            start: e
        })
    }

    const handleEndDate = (e) => {
        setEndDate(e)
        setFormValues({
            ...formValues,
            end: e
        })
    }

    const handleInputChange = ({ target }) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value
        })
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        
        const momentStart = moment(startDate)
        const momentEnd = moment(endDate)
        
        if(momentStart.isSameOrAfter(momentEnd)) {
            setEndDateIsValid(false)
            return
        }

        if(title.trim().length <= 2 ) {
            console.log('El título ha de tener al menos 3 carácteres');
            setTitleIsValid(false)
            return
        }

        setEndDateIsValid(true)
        setTitleIsValid(true)

        if ( activeEvent ) {
            console.log(formValues);
            dispatch(startEventUpdated(formValues))
        } else {
            dispatch(eventStartAddNew(formValues))
        }     
        dispatch(uiCloseModal())
        dispatch(unsetActive())
        setFormValues( initModalContent )

    }

    useEffect(() => {
      
        if(activeEvent) {
            setFormValues(activeEvent)
        }

    }, [activeEvent]);

    return (
        <div>
            <h1> { activeEvent ? 'Editar evento' : 'Nuevo evento' } </h1>
            <hr />
            <form 
                className="container"
                onSubmit={ handleSubmit }
            >

                <div className="form-group">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker
                        className="form-control"
                        onChange={handleStartDate}
                        value={ startDate }
                        name='start'
                    />
                </div>

                <div className="form-group">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker
                        className={`form-control ${!endDateIsValid ? 'formBorderValidationError' : ''}`}
                        onChange={handleEndDate}
                        minDate={ startDate }
                        value={ endDate }
                        name='end'
                    />
                    {
                        !endDateIsValid &&
                        <p className='formTextValidationError'>La fecha fin debe de ser mayor a la fecha de inicio</p>
                    }
                </div>

                <hr />
                <div className="form-group">
                    <label>Titulo y notas</label>
                    <input
                        type="text"
                        className={`form-control ${!titleIsValid ? 'formBorderValidationError' : ''}`}
                        placeholder="Título del evento"
                        name="title"
                        value={ title }
                        onChange={ handleInputChange }
                        autoComplete="off"
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
                    {
                        !titleIsValid &&
                        <p className='formTextValidationError'>El título ha de tener al menos 3 carácteres</p>
                    }
                </div>

                <div className="form-group">
                    <textarea
                        type="text"
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={ notes }
                        onChange={ handleInputChange }
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                    
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </div>
    )
};
