import { useEffect } from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import { useDispatch } from "react-redux";

import { LoginScreen } from "../components/auth/LoginScreen";
import { CalendarScreen } from "../components/calendar/CalendarScreen";
import { startChecking } from "../actions/auth";
import { PublicRoutes } from "./PublicRoutes";
import { useSelector } from "react-redux";
import { PrivateRoutes } from "./PrivateRoutes";

export const AppRouter = () => {

    const dispatch = useDispatch()
    const { checking, uid } = useSelector( state => state.auth)
    const isAuthenticated = uid ? true : false

    useEffect(() => {
        dispatch(startChecking())
    }, [dispatch]);

    if(checking) {
        return( <h5>Espere...</h5>)
    }

    return (
        <div>
            <Router>
                <Switch>

                    <PublicRoutes isAuthenticated={isAuthenticated} path="/login" exact component={ LoginScreen } />
                    <PrivateRoutes isAuthenticated={isAuthenticated} path="/" exact component={ CalendarScreen } />

                    {/*
                        <Route path="/login" exact>
                            <LoginScreen />
                        </Route>

                        <Route path="/" exact>
                            <CalendarScreen />
                        </Route>
                    */}
                    

                    <Redirect to="/" />
                </Switch>
            </Router>
        </div>
    )
};
