import { types } from "../types/types"


const initialState = {
    events: [],
    activeEvent: null
}

export const calendarReducer = (state = initialState, action) => {

    switch (action.type) {

        case types.eventSetActive:
            return {
                ...state,
                activeEvent: action.payload
            }
        
        case types.eventUnsetActive:
            return {
                ...state,
                activeEvent: null
            }
        
        case types.eventAddNew:
            return {
                ...state,
                events: [
                    ...state.events,
                    action.payload
                ]
            }
        
        case types.eventUpdate:
            return {
                ...state,
                events:
                    state.events.map(event => (event.id === action.payload.id) ? action.payload : event)
                
            }
        
        case types.eventDelete:
            return {
                ...state,
                events:
                    state.events.filter(event => (event.id !== state.activeEvent.id) && event),
                activeEvent: null
            }

        case types.eventsLoaded:
            
            return {
                ...state,
                events: [
                    ...action.payload
                ]                   
            }

        default:
            return state
    }
}