import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import { addNotification } from "./notification"
import { types } from "../types/types"

import { v4 } from "uuid"

export const startLogin = (email, password) => {
    return async (dispatch) => {
        const response = await fetchSinToken('auth/', { email, password }, 'POST')
        const body = await response.json()

        if (body.ok) {
            localStorage.setItem('token', body.token)
            localStorage.setItem('tokenInitDate', new Date().getTime())
            return dispatch(login({
                name: body.name,
                uid: body.uid
            }))
        }

        return dispatch(addNotification(v4(), body.msg, types.error))

    }
}

export const startRegister = (name, email, password) => {
    return async (dispatch) => {
        
        const response = await fetchSinToken('auth/new', { name, email, password }, 'POST')
        const body = await response.json()
        console.log(body);
        if (body.ok) {
            console.log(body);
            localStorage.setItem('token', body.token)
            localStorage.setItem('tokenInitDate', new Date().getTime())
            return dispatch(login({
                name: body.name,
                uid: body.uid
            }))
        }

        return dispatch(addNotification(v4(), body.msg, types.error))

    }

}

export const startChecking = () => {
    return async (dispatch) => {

        const isCurrentToken = !!(localStorage.getItem('token') || '');
 
        if (!isCurrentToken){
            dispatch(finishChecking()); 
            return;
        }

        const response = await fetchConToken('auth/renew')
        const body = await response.json()
        
        if (body.ok) {
            console.log(body);
            localStorage.setItem('token', body.token)
            localStorage.setItem('tokenInitDate', new Date().getTime())
            return dispatch(login({
                name: body.name,
                uid: body.uid
            }))
        }
        

        return dispatch(finishChecking())
    }
}

export const startLogout = () => {
    return (dispatch) => {
        localStorage.clear()
        dispatch(logout())
    }
}

const logout = () => {
    return {
        type: types.authLogOut
    }
}

const finishChecking = () => {
    return {
        type: types.authFinishChecking
    }
}

const login = (user) => {
    return {
        type: types.authLogin,
        payload: user
    }
}