import { useState } from "react";
import { useForm } from "../../hooks/useForm";
import validator from "validator";
import { useDispatch } from "react-redux";
import { addNotification } from "../../actions/notification";
import { v4 } from "uuid";
import { types } from "../../types/types";
import { startLogin } from "../../actions/auth";

export const Login = () => {

    const dispatch = useDispatch()

    const initialState = {
        email: 'i@i.com',
        password: '123456'
    }

    const [ formLoginValues, handleLoginInputChange] = useForm(initialState)
    const [isEmailValid, setIsEmailValid] = useState(true);
    const [isPasswordValid, setIsPasswordValid] = useState(true);

    const { email, password } = formLoginValues

    const handleLoginSubmit = (e) => {
        e.preventDefault()
        if(validarCampos()) {
            dispatch(startLogin(email, password))
        }
    }

    const validarCampos = () => {

        if(!validator.isEmail(email)) {
            setIsEmailValid(false)
            dispatch(addNotification(v4(), 'Correo no válido', types.error))
            return false
        }
        setIsEmailValid(true)
        
        if( password.trim().length <= 5 ) {
            setIsPasswordValid(false)
            console.log('passwrod mal');
            dispatch(addNotification(v4(), 'La contraseña ha de ser de al menos 6 carácteres', types.error))
            return false
        }
        setIsPasswordValid(true)
        
        return true

    }

    return (
        <div className="col-md-6 login-form-1">
            <h3>Ingreso</h3>
            <form
                onSubmit={handleLoginSubmit}
            >
                <div className="form-group">
                    <input
                        type="text"
                        name="email"
                        className={`form-control ${!isEmailValid ? 'formBorderValidationError' : '' } `}
                        placeholder="Correo"
                        value={email}
                        onChange={handleLoginInputChange}
                    />
                </div>
                <div className="form-group">
                    <input
                        type="password"
                        name="password"
                        className={`form-control ${!isPasswordValid ? 'formBorderValidationError' : '' } `}
                        placeholder="Contraseña"
                        value={password}
                        onChange={handleLoginInputChange}
                    />
                </div>
                <div className="form-group">
                    <input
                        type="submit"
                        className="btnSubmit"
                        value="Login"
                    />
                </div>
            </form>
        </div>
    )
};
